  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.counter {
    font-size: 20px;
    background-color: #782333;
    padding: 10px;
    margin-top: 10px;
    color:#fff;
    font-weight: bold;
}


.header {
    background-color: yellow;
    padding: 20px 10px;
    color: #000;
    text-transform: uppercase;
}
.pacman {
    height: 100px;
    position: relative;
    background: #745365
}

.pacman__figure {
    position: absolute;
    top: 10px;
    left: 0;
    background-color: yellow;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    -webkit-animation: move 5s linear infinite alternate, mirror 10s step-end infinite;
            animation: move 5s linear infinite alternate, mirror 10s step-end infinite;
}

.pacman__figure::before {
    content: '';
    position: absolute;
    top: 5px;
    right: 30px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #000;
    -webkit-animation: eyemove .25s linear infinite alternate;
            animation: eyemove .25s linear infinite alternate;
}

.pacman__figure::after {
    content: '';
    position: absolute;
    right: 0;
    top: 50%;
    border-style: solid;
    border-color: transparent;
    border-width: 30px;
    border-right-style: solid;
    border-right-color: #000;
    border-right-width: 40px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    -webkit-animation: eat .25s linear infinite alternate;
            animation: eat .25s linear infinite alternate;
}

@-webkit-keyframes move {
    from {
        left: 0;
    }
  
    to {
        left: calc(100% - 80px);
    }
}

@keyframes move {
    from {
        left: 0;
    }
  
    to {
        left: calc(100% - 80px);
    }
}

@-webkit-keyframes mirror {
    from {
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
    }

    50% {
        -webkit-transform: scale(-1, 1);
                transform: scale(-1, 1);
    }

    to {
        -webkit-transform: scale(-1, 1);
                transform: scale(-1, 1);
    }
}

@keyframes mirror {
    from {
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
    }

    50% {
        -webkit-transform: scale(-1, 1);
                transform: scale(-1, 1);
    }

    to {
        -webkit-transform: scale(-1, 1);
                transform: scale(-1, 1);
    }
}

@-webkit-keyframes eat {
    from {
        border-width: 30px;
        border-right-width: 40px;
    }
  
    to {
        border-width: 0;
        border-right-width: 40px;
    }
}

@keyframes eat {
    from {
        border-width: 30px;
        border-right-width: 40px;
    }
  
    to {
        border-width: 0;
        border-right-width: 40px;
    }
}

@-webkit-keyframes eyemove {
    from {
        top: 5px;
        right: 30px;
    }
  
    to {
        top: 15px;
        right: 20px;
    }
}

@keyframes eyemove {
    from {
        top: 5px;
        right: 30px;
    }
  
    to {
        top: 15px;
        right: 20px;
    }
}

@media (max-width: 767.98px) {
    .pacman__figure {
        -webkit-animation-duration: 2s, 4s;
                animation-duration: 2s, 4s;
    }
}
.avia-sales__prices {
    list-style: none;
    margin: .5em auto;
    padding: 0 1em;
    display: none;
}

.avia-sales__prices--open {
    display: block;
}

.avia-sales__item {
    display: block;
    padding: .5em;
    background-color: khaki;
    color: midnightblue;
    margin: .3em 0;
    border-radius: 1em;
    text-align: center;
}

.avia-sales__open {
    font-size: .9em;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border: none;
    background-color: transparent;
    color: darkblue;
}

.avia-sales__error {
    color: red;
    padding: 0 1em;
}

@media (min-width: 576px) { 
    .avia-sales__prices {
        width: 	540px;
    }

    .avia-sales__item {
        display: inline-block;
        padding: .5em 1em;
        margin: .3em;
    }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) { 
    .avia-sales__prices {
        width: 720px;
    }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) { 
    .avia-sales__prices {
        width: 960px;
    }
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) { 
    .avia-sales__prices {
        width: 1140px;
    }
}

.App {
